.contact {
  font-size: 25px;
  color: var(--primary-text-color);
  font-weight: 500;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
}

.contact_item {
  margin-left: 0;
}

.btn {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 15px;
  line-height: 1.5;
  cursor: pointer;
  letter-spacing: 0.04em;
  color: var(--primary-text-color);
  background:  #f7f8ffb8;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  min-width: 80px;
  height: 35px;
  margin-left: 25px;
  padding-top: 7px;
  padding-right: 16px;
  padding-bottom: 8px;
  padding-left: 16px;
  border: 0;
  transition: background-color 250ms var(--timing-function);
}

.btn:is(:hover, :focus) {
  background: rgb(52,246,134);
  background: radial-gradient(circle, rgba(52,246,134,1) 0%, rgba(50,168,226,1) 100%);
  color: black;
}
